:root {
  /* New palette (from your attachment) */
  --sage: #ACBDAA;    /* soft green */
  --navy: #1E2D4C;    /* deep navy */
  --muted-gray: #858585;
  --beige: #CEC0BB;   /* warm beige */

  /* Mapped site tokens */
  --primary-color: var(--sage);
  --secondary-color: var(--navy);
  --accent-color: var(--beige);
  --bg-color: #EAF9E7; /* pale green background from the attachment */
  --card-bg: var(--beige);
  --text-dark: var(--navy);
  --text-light: var(--muted-gray);
  --nav-bg: rgba(255,255,255,0.92);

  --max-width: 1100px;
  --card-radius: 14px;
  --transition-bounce: cubic-bezier(0.36, 0, 0.66, -0.56);
  --transition-smooth: cubic-bezier(0.17, 0.55, 0.55, 1);
}