/* Mobile Navigation */
@media (max-width: 768px) {
  .nav-hamburger {
    display: block;
  }

  .nav-menu {
    display: none;
    position: absolute;
    top: 70px;
    left: 0;
    right: 0;
    background: white;
    padding: 1rem;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }

  .nav-menu.active {
    display: flex;
  }

  .nav-menu li {
    width: 100%;
    text-align: center;
  }

  .nav-menu li a {
    display: block;
    padding: 1rem;
    width: 100%;
  }

  .nav-menu li a:hover {
    background: rgba(124, 58, 237, 0.1);
  }
}